/*
OFFICIAL DENYCONFORMITY.COM COLOR PALLETTEDETE

COLOR		RESTING		ACTIVE

BLUE		#0066FF		#6699FF
RED			#cc0000		#ff6666
YELLOW		#ffcc00		#ffff99
GREEN		#006600		#039114
PURPLE		#663399		#9933cc
GREY		#404040		#666666

ORANGE		#ff6600		#ff9933

*/
$dc-blue: #0066FF;
$dc-red: #CC0000;
$dc-yellow: #FFCC00;
$dc-green: #006600;
$dc-purple: #663399;
$dc-grey: #404040;
$dc-orange: #f60;

$shadow-color: rgba(0,0,0,0.12);

$card-shadow: 0px 5px 5px -3px $shadow-color,
              0px 8px 10px 1px $shadow-color,
              0px 3px 14px 2px $shadow-color;

$card-quiet-shadow: 0px 2px 1px -1px $shadow-color,
                    0px 1px 1px 0px $shadow-color,
                    0px 1px 3px 0px $shadow-color;

// media query names
$media-mobile: "only screen and (max-width: 500px)";

$color-highlight: rgba(255, 243, 230, 0.5);
$color-shadow: rgba(77, 38, 0, 0.4);
$color-text: #111;

@mixin font {
  font-family: "Cormorant Garamond", serif;
}

@mixin font-title {
  font-family: "Lovers Quarrel", cursive;
}

@mixin font-subtitle {
  font-family: "Playfair Display", serif;
}
